.wrapper {
    display: flex;
    align-items: center;
    max-width: 42rem;
    margin: 1rem auto;
    padding: 1rem;
    background: #f3f3f3;
    box-shadow: 0.188rem 0.188rem #855f49, -1em 0 .4em #d4c3b4;
    border-radius: 0.5rem;
}

.text {
    padding: 1rem;
}

.image img {
    max-width: 20rem;
    border-radius: 0.5rem;
    transition: transform 0.3s ease-in-out;
}

.image img:hover {
    transform: scale(1.05);
}

@media (max-width: 720px) {
    .wrapper {
        max-width: 26rem;
        margin: 1rem auto;
        padding: 1rem;
    }

    .image img {
        max-width: 12rem;
    }

    .text span {
        display: none;
    }
}

@media (max-width: 480px) {
    .wrapper {
        max-width: 16rem;
        margin: 1rem auto;
        padding: 1rem;
        display: inline-block;
    }

    .image img {
        max-width: 12rem;
    }

    .text span {
        display: none;
    }
}